.socials {
  @include list-style;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.socials__item {
  width: 62px;
  height: 46px;
}

.socials__link {
  display: block;
  padding: 12px 20px;
  fill: #231f20;

  &--fb {
    padding: 7px 21px;
  }

  &--tw {
    padding: 12px 13px;
  }
}

@media (min-width: $desktop-width) {
  .socials__link:hover svg,
  .socials__link:focus svg {
    fill: $primary-color;
  }

  .socials__link:active svg {
    opacity: 0.3;
  }
}
